<template>
  <div class="container">
    <!-- <el-row>
      <el-select v-model="region" placeholder="请选择所属区域" filterable clearable style="width: 178px;" @change="getBoardSet">
        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value" />
      </el-select>
    </el-row> -->
    <el-carousel direction="vertical" :interval="(kanbanSetInfo.time - 0) * 1000" :autoplay="kanbanSetInfo.play" loop arrow="never" indicator-position="none" @change="carouselChange">
      <el-carousel-item v-for="(item, i) in kanbanList" :key="i">
        <el-row :gutter="20" style="height: calc(33.3vh - 20px);">
          <template v-for="(chartItem, j) in item.chartTemplates">
            <el-col v-if="chartItem.id === 1" :key="j" style="height: 100%;" :span="8">
              <!-- 库存占比 -->
              <chart-com-1 ref="ChartCom1" :key="viewKey" style="height: 100%;" :title="chartItem.name" v-bind="$attrs" />
            </el-col>
            <el-col v-if="chartItem.id === 2" :key="j" style="height: 100%;" :span="8">
              <!-- 账龄占比 -->
              <chart-com-2 ref="ChartCom2" :key="viewKey" style="height: 100%;" :title="chartItem.name" v-bind="$attrs" />
            </el-col>
            <el-col v-if="chartItem.id === 3" :key="j" style="height: 100%;" :span="8">
              <!-- 月度库存 -->
              <chart-com-3 ref="ChartCom3" :key="viewKey" style="height: 100%;" :title="chartItem.name" v-bind="$attrs" />
            </el-col>
            <el-col v-if="chartItem.id === 4" :key="j" style="height: 100%;" :span="8">
              <!-- 库存周转率 -->
              <chart-com-4 ref="ChartCom4" :key="viewKey" style="height: 100%;" :title="chartItem.name" v-bind="$attrs" />
            </el-col>
            <el-col v-if="chartItem.id === 5" :key="j" style="height: 100%;" :span="8">
              <!-- 库存账龄 -->
              <chart-com-5 ref="ChartCom5" :key="viewKey" style="height: 100%;" :title="chartItem.name" v-bind="$attrs" />
            </el-col>
            <el-col v-if="chartItem.id === 6" :key="j" style="height: 100%;" :span="8">
              <!-- 库存账龄占比 -->
              <chart-com-6 ref="ChartCom6" :key="viewKey" style="height: 100%;" :title="chartItem.name" v-bind="$attrs" />
            </el-col>
            <el-col v-if="chartItem.id === 7" :key="j" style="height: 100%;" :span="8">
              <!-- 在途物资分析 -->
              <chart-com-7 ref="ChartCom7" :key="viewKey" style="height: 100%;" :title="chartItem.name" v-bind="$attrs" />
            </el-col>
            <el-col v-if="chartItem.id === 8" :key="j" style="height: 100%;" :span="8">
              <!-- 物料价格分析 -->
              <chart-com-8 ref="ChartCom8" :key="viewKey" style="height: 100%;" :title="chartItem.name" v-bind="$attrs" />
            </el-col>
            <el-col v-if="chartItem.id === 9" :key="j" style="height: 100%;" :span="8">
              <!-- 合同到货分析 -->
              <chart-com-9 ref="ChartCom9" :key="viewKey" style="height: 100%;" :title="chartItem.name" v-bind="$attrs" />
            </el-col>
            <el-col v-if="chartItem.id === 10" :key="j" style="height: 100%;" :span="8">
              <!-- 供应商综合分析 -->
              <chart-com-10 ref="ChartCom10" :key="viewKey" style="height: 100%;" :title="chartItem.name" v-bind="$attrs" />
            </el-col>
            <el-col v-if="chartItem.id === 11" :key="j" style="height: 100%;" :span="8">
              <!-- 供应商价格趋势 -->
              <chart-com-11 ref="ChartCom11" :key="viewKey" style="height: 100%;" :title="chartItem.name" v-bind="$attrs" />
            </el-col>
          </template>
        </el-row>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
// 库存占比 账龄占比 月度库存 库存周转率 库存账龄 库存账龄占比
// const span8 = [1, 2, 3, 4, 5, 6]
// 在途物资分析 物料价格分析 供应商综合分析 供应商价格趋势
// const span12 = [7, 8, 10, 11]
// 合同到货分析
// const span24 = [9]
import ChartComponents from './components' // 批量引入的chart组件
import { getBoardSetApi, getBoardDetailApi } from '@/apis/modules/analysis/board'
export default {
  components: {
    ...ChartComponents
  },
  data() {
    return {
      viewKey: 1,
      options: [
        { label: '科技园', value: 1 },
        { label: '中院', value: 2 }
      ],
      region: 1,
      kanbanSetInfo: {},
      kanbanList: []
    }
  },
  mounted() {
    this.getBoardSet()
  },
  methods: {
    // 获取区域下的看板设置信息
    getBoardSet() {
      const params = {
        region: this.region
      }
      return new Promise((resolve, reject) => {
        getBoardSetApi(params)
          .then(async res => {
            const { code, data } = res
            if (!code) {
              this.kanbanSetInfo = { ...data }
              const ids = data.billboardsIds.split(',')
              const res = await Promise.all(ids.map(id => this.getBoardDetail(id)))
              console.log('res88888888', res)
              this.kanbanList = res.map(item => item.data)
              console.log('res88999999', this.kanbanList)
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // 获取看板详情
    getBoardDetail(id = 0) {
      const params = { id }
      return new Promise((resolve, reject) => {
        getBoardDetailApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              console.log('获取看板详情data', data)
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    carouselChange(index) {
      this.reset()
    },
    reset() {
      this.viewKey++
      console.log(this.$refs.ChartCom1)
      this.$refs.ChartCom1 && this.$refs.ChartCom1[0].resize()
      this.$refs.ChartCom2 && this.$refs.ChartCom2[0].resize()
      this.$refs.ChartCom3 && this.$refs.ChartCom3[0].resize()
      this.$refs.ChartCom4 && this.$refs.ChartCom4[0].resize()
      this.$refs.ChartCom5 && this.$refs.ChartCom5[0].resize()
      this.$refs.ChartCom6 && this.$refs.ChartCom6[0].resize()
      this.$refs.ChartCom7 && this.$refs.ChartCom7[0].resize()
      this.$refs.ChartCom8 && this.$refs.ChartCom8[0].resize()
      this.$refs.ChartCom9 && this.$refs.ChartCom9[0].resize()
      this.$refs.ChartCom10 && this.$refs.ChartCom10[0].resize()
      this.$refs.ChartCom11 && this.$refs.ChartCom11[0].resize()
    }
  }
}
</script>

<style lang="scss" scoped>
// .container {
// }
::v-deep .el-carousel {
    .el-carousel__container {
        height: 100vh;
    }
}
.el-col {
    margin-bottom: 20px;
}
</style>
